.ant-breadcrumb{box-sizing:border-box;margin:0;padding:0;color:#22c2fe;font-variant:tabular-nums;line-height:1.5;list-style:none;font-feature-settings:"tnum";color:#333;font-size:14px}.ant-breadcrumb .anticon{font-size:14px}.ant-breadcrumb a{color:#333;transition:color .3s}.ant-breadcrumb a:hover{color:#4ad5ff}.ant-breadcrumb>span:last-child{color:#22c2fe}.ant-breadcrumb>span:last-child a{color:#22c2fe}.ant-breadcrumb>span:last-child .ant-breadcrumb-separator{display:none}.ant-breadcrumb-separator{margin:0 8px;color:#333}.ant-breadcrumb-link>.anticon+span{margin-left:4px}.ant-breadcrumb-overlay-link>.anticon{margin-left:4px}